<template>
 <div>
   <form class="login" @submit.prevent='logIn()'>
     <h1>Вход</h1>
     <label>Email</label>
     <input required v-model="User.email" type="email" aria-describedby="emailHelp" placeholder="test@gmail.com"/><br>
     <label>Пароль</label>
     <input required v-model="User.password" type="password" placeholder="Pa$$w0rd"/><br/>    
     <button type="submit" class="game_button_add">Войти</button>
   </form>
 </div>
</template>

<script>
import { mapActions, mapGetters} from 'vuex'

export default {
  data(){
      return{
        User:{email:'', password:''},
      }
  },
  methods:{
    ...mapActions('auth',['login']),
    ...mapActions('auth',['updateUser']),
    ...mapGetters('auth',['isAuthenticated']),
      async logIn(){
        try {
          await this.login(this.User);
          this.$router.push('/profile')
        }
        catch(ex) {
          console.log(ex.response);
          console.log(document.cookie)
        }
      },
    }
}
</script>
<style>
.game_button_add{
  border-radius: 5px;
  text-decoration: none;
  display: inline-block;
  padding: 5px 10px;
  background-image: linear-gradient(45deg, #6ab1d7 0%, #33d9de 50%, #002878 100%);
  background-position: 100% 0;
  background-size: 200% 200%;
  color: white;
  box-shadow: 0 16px 32px 0 rgba(0, 40, 120, .35);
  transition: .5s;
  margin:2px;
}
.game_button_add:hover{
  box-shadow: 0 0 0 0 rgba(0, 40, 120, 0);
  background-position: 0 0;
}
</style>